<template>
  <div class="course-screen-question">
    <head-top title="截屏提问">
      <span slot="right" @click="toClose">
        <van-icon class-prefix="iconfont icon-jieshu" size="2.3rem" color="#fff" class="head-right"></van-icon>
      </span>
    </head-top>

    <van-field name="uploader" label="上传图片">
      <template #input>
        <van-uploader v-model="uploader" :max-count="1" :before-read="beforeRead" :after-read="afterRead"/>
      </template>
    </van-field>

    <van-field name="radio" label="题目类型">
      <template #input>
        <van-radio-group v-model="radio" direction="horizontal">
          <van-radio name="1">单选</van-radio>
          <van-radio name="2">多选</van-radio>
          <van-radio name="3">判断</van-radio>
          <van-radio name="4">问答</van-radio>
        </van-radio-group>
      </template>
    </van-field>
    <van-field name="stepper" label="题目选项">
      <template #input>
        <van-stepper v-model="optionsNum" integer min="2" max="10"></van-stepper>
        <span>（选项A到{{options[optionsNum-1]}}）</span>
      </template>
    </van-field>
    <van-field name="stepper" label="答题时间">
      <template #input>
        <van-stepper v-model="answerTimes" integer min="0"></van-stepper>
        <span>分钟（0不限时）</span>
      </template>
    </van-field>
    <van-field name="stepper" label="题目分数">
      <template #input>
        <van-stepper integer min="0" v-model="answerScore"></van-stepper>
      </template>
    </van-field>

    <van-field name="button" label="下发方式">
      <template #input>
        <div class="flex-between" style="width: 100%;">
          <van-button type="primary" size="small" :disabled="startAnswer" v-show="!startAnswer || downWay===1"
                      @click="toAll">
            全员作答
          </van-button>
          <van-button type="primary" size="small" :disabled="startAnswer" v-show="!startAnswer || downWay===2"
                      @click="toRandom">
            随机挑人
          </van-button>
          <van-button type="primary" size="small" :disabled="startAnswer" v-show="!startAnswer || downWay===3"
                      @click="toResponder">
            抢答
          </van-button>
          <van-button type="danger" size="small" v-show="startAnswer" @click="toStop">结束答题</van-button>
        </div>
      </template>
    </van-field>

    <div class="sub_nav">
      <span>选中成员</span>
      <van-button type="primary" size="small" @click="startRandom">
        {{randomStudents.length>0?'重新挑人':'开始挑人'}}
      </van-button>
    </div>
    <div class="sub_module">
      <span v-if="randomStudents.length===0">暂无人员</span>
      <div class="flex-wrap-between" v-else>
        <div v-for="(sub, k) in randomStudents" :key="k" class="student_card van-hairline--surround">
          <img class="col_4" :src="sub.imgSrc" alt=""/>
          <div>{{sub.studentName}}</div>
          <van-tag type="primary" v-if="sub.isResponse">已提交</van-tag>
          <van-tag type="warning" v-else>未作答</van-tag>
        </div>
        <div class="student_card" style="width: 19vw;" v-for="d in Math.abs( randomStudents.length % 4 - 4)" :key="-d">
        </div>
      </div>
    </div>

    <div class="sub_nav">
      <span>抢答</span>
      <van-button type="primary" size="small" @click="startResponse">开始抢答</van-button>
    </div>
    <div class="sub_module">
      <div class="flex-between van-hairline--bottom">
        <span>抢答状态</span>
        <van-tag type="danger" v-if="responderPerson">已结束</van-tag>
        <van-tag type="warning" v-else>抢答中</van-tag>
      </div>
      <div class="flex-between van-hairline--bottom">
        <span>抢答时间</span>
        <span>{{responderTime}}</span>
      </div>
      <div class="flex-between" v-if="responderPerson">
        <span>抢中人员</span>
        <div class="student_card van-hairline--surround">
          <img class="col_4" :src="responderPerson.imgSrc" alt=""/>
          <div>{{responderPerson.studentName}}</div>
          <van-tag type="primary" v-if="responderPerson.isResponse">已提交</van-tag>
          <van-tag type="warning" v-else>未作答</van-tag>
        </div>
      </div>
    </div>

    <div class="sub_nav">
      <span>提交人员</span>
    </div>
    <div class="sub_module flex-wrap-between">
      <div v-for="(sub, k) in allResponseStudents" :key="k" class="student_card van-hairline--surround">
        <img class="col_4" :src="sub.imgSrc" alt=""/>
        <div>{{sub.studentName}}</div>
        <van-tag type="primary" v-if="sub.isResponse">已提交</van-tag>
        <van-tag type="warning" v-else>未作答</van-tag>
      </div>
      <div class="student_card" style="width: 19vw;" v-for="d in Math.abs( randomStudents.length % 4 - 4)"
           :key="-d"></div>
    </div>

    <div v-show="realAnswer" class="sub_nav">
      <span>正确答案</span>
    </div>
    <div v-show="realAnswer" class="sub_module">
      <pre>{{realAnswer}}</pre>
    </div>

    <div class="sub_nav">
      <span>调研情况</span>
      <van-button type="info" size="small" @click="toSurvey">开始调研</van-button>
    </div>
    <div class="sub_module flex-wrap-between">
      <div v-for="(sub, k) in surveyStudents" :key="k" class="student_card van-hairline--surround">
        <img class="col_4" :src="sub.imgSrc" alt=""/>
        <div>{{sub.studentName}}</div>
        <van-tag type="success" v-if="sub.master">已掌握</van-tag>
        <van-tag type="danger" v-else>未掌握</van-tag>
      </div>
      <div class="student_card" style="width: 19vw;" v-for="d in Math.abs( randomStudents.length % 4 - 4)"
           :key="-d"></div>
    </div>

    <van-popup v-model="selectAnswerShow" position="bottom">
      <van-cell>
        <span slot="icon" @click="selectAnswerShow = false" style="color: #1989fa;">取消</span>
        <span slot="title">
          {{radio=== '4' ? '请填写答案' : '请选择正确答案'}}
        </span>
        <span slot="right-icon" @click="onConfirm" style="color: #1989fa;">确定</span>
      </van-cell>

      <van-radio-group v-if="radio==='1'" v-model="answer" style="text-align: left;">
        <van-cell-group>
          <van-cell v-for="item in answers" :key="item" :title="item" clickable @click="answer = item">
            <van-radio slot="right-icon" :name="item"></van-radio>
          </van-cell>
        </van-cell-group>
      </van-radio-group>

      <van-checkbox-group v-else-if="radio==='2'" v-model="multiAnswer" style="text-align: left;">
        <van-cell-group>
          <van-cell v-for="item in answers" :key="item" :title="item" clickable @click="answer = item">
            <van-checkbox slot="right-icon" sharp="square" :name="item"></van-checkbox>
          </van-cell>
        </van-cell-group>
      </van-checkbox-group>

      <van-radio-group v-if="radio==='3'" v-model="answer" style="text-align: left;">
        <van-cell-group>
          <van-cell v-for="item in judgeValues" :key="item" :title="item" clickable @click="answer = item">
            <van-radio slot="right-icon" :name="item"></van-radio>
          </van-cell>
        </van-cell-group>
      </van-radio-group>

      <van-field v-else v-model="answer" name="答案" label="" placeholder="请填写答案" type="textarea" maxlength="100"
                 autofocus autosize show-word-limit :rules="[{ required: true, message: '请填写答案' }]">
      </van-field>
    </van-popup>
  </div>
</template>

<script>
import defaultImg from "../../../../assets/boy_img.png";
import { Dialog } from "vant";
import { newGuid } from "@/utils/index";

const firsts = ['许', '张', '吴', '王', '林', '郭', '史', '黄', '陈', '黄', '郑', '关', '刘', '曹', '毛', '方', '杨', '施', '李', '杜', '诸葛', '马'];
const names = ['歩', '弥', '鑫', '霞', '晓', '菲', '坚', '浩', '智', '志', '梅', '雪', '林', '皇', '芳', '丽', '冬', '良', '宇', '荷', '妮', '梦'];
const sNos = ['1619112002', '1619112003', '1619112004', '1619112005', '1619112006', '1619112007', '1619112008', '1619112009', '1619112010', '1619112011', '1619112012', '1619112013', '1619112014', '1619112015', '1619112016', '1619112017', '1619112018', '1619112019', '1619112020', '1619112021', '1619112022'];

export default {
  name: "screenQuestion",
  data () {
    return {
      studentList: [],
      options: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J'],
      judgeValues: ['对', '错'],
      uploader: [],
      radio: '3',
      optionsNum: 4,
      answerTimes: 0,
      answerScore: 0,

      downWay: -1,
      startAnswer: false,
      allResponseStudents: [],
      randomStudents: [],
      responderTime: "00:00",
      responderPerson: null,

      answer: "",
      multiAnswer: [],
      realAnswer: "",
      selectAnswerShow: false,

      surveyStudents: []
    };
  },
  computed: {
    answers () {
      if (['1', '2'].indexOf(this.radio) > -1) {
        return this.options.slice(0, this.optionsNum);
      } else if (this.radio === '3') {
        return ['对', '错'];
      } else {
        return [];
      }
    }
  },
  mounted () {
    this.init();
  },
  methods: {
    init () {
      for (let i = 0; i < 15; i++) {
        this.studentList.push({
          imgSrc: defaultImg,
          studentID: newGuid(),
          studentName: firsts[this.randomIndex(20)] + names[this.randomIndex(20)] + (this.randomIndex(20) % 2 === 0 ? names[this.randomIndex(20)] : ''),
          studentSno: sNos[this.randomIndex(20)] + '0' + this.randomIndex(20),
          studentSex: (this.randomIndex(20) % 2 + 1).toString(),
          isResponse: 0
        });
      }
    },
    randomIndex (max) {
      return Math.round(max * Math.random());
    },
    toClose () {
      Dialog.confirm({
        title: '提示',
        message: '是否结束当前题目？'
      }).then(() => {
        this.$router.replace("/course/screen-question");
        // on confirm
      }).catch(() => {
        // on cancel
      });
    },
    // 返回布尔值
    beforeRead (file) {
      if (file.type !== 'image/jpeg') {
        this.$toast('请上传 jpg 格式图片');
        return false;
      }
      return true;
    },
    afterRead (file) {
      file.status = 'uploading';
      file.message = '上传中...';

      setTimeout(() => {
        // file.status = 'failed';
        // file.message = '上传失败';
        file.status = 'success';
        file.message = '';
      }, 1000);
    },
    toAll () {
      this.downWay = 1;
      this.startAnswer = true;
      for (let i = 0; i < this.studentList.length; i++) {
        setTimeout(() => {
          const students = this.studentList.filter(a => {
            return !this.allResponseStudents.find(b => a.studentID === b.studentID);
          });
          if (students.length === 0) {
          } else {
            const randomValue = this.randomIndex(students.length - 1);
            this.allResponseStudents.splice(0, 0, students[randomValue]);
            const item = this.allResponseStudents[0];
            item.isResponse = 1;
          }
        }, 1000 * this.randomIndex(20));
      }
    },
    toRandom () {
      this.downWay = 2;
      this.startAnswer = true;
    },
    toResponder () {
      this.downWay = 3;
      this.startAnswer = true;
    },
    toStop () {
      this.selectAnswerShow = true;
      this.downWay = -1;
      this.answer = "";
      this.multiAnswer = [];
      this.startAnswer = false
    },
    startRandom () {
      const students = this.studentList.filter(a => {
        return !this.randomStudents.find(b => a.studentID === b.studentID);
      });
      if (students.length === 0) {
        this.$toast("学生已选完！");
        return;
      }
      const randomValue = this.randomIndex(students.length - 1);
      this.randomStudents.splice(0, 0, students[randomValue]);
      const item = this.randomStudents[0];
      setTimeout(() => {
        item.isResponse = 1;
      }, 1000 * this.randomIndex(20));
    },
    startResponse () {
      this.startTime(0);
      setTimeout(() => {
        const randomValue = this.randomIndex(this.studentList.length - 1);
        this.responderPerson = this.studentList[randomValue];
        setTimeout(() => {
          this.responderPerson.isResponse = 1;
        }, 1000 * this.randomIndex(20));
      }, 1000 * this.randomIndex(20));
    },
    startTime (value) {
      if (this.responderPerson) return;
      const m = Math.floor(value / 60);
      const s = value % 60;
      this.responderTime = (m > 9 ? m : '0' + m) + ':' + (s > 9 ? s : '0' + s);
      setTimeout(() => {
        value++;
        this.startTime(value);
      }, 1000);
    },
    onConfirm () {
      console.log(this.answer);
      console.log(this.multiAnswer);
      if (!this.answer && this.multiAnswer.length === 0) {
        this.$toast(this.radio === "4" ? "请先输入答案" : "请先选择答案");
      } else {
        this.realAnswer = this.answer || this.multiAnswer.sort().join('');
        this.selectAnswerShow = false;
      }
    },
    toSurvey () {
      for (let i = 0; i < this.studentList.length; i++) {
        setTimeout(() => {
          const students = this.studentList.filter(a => {
            return !this.allResponseStudents.find(b => a.studentID === b.studentID);
          });
          if (students.length === 0) {
          } else {
            const randomValue = this.randomIndex(students.length - 1);
            this.surveyStudents.splice(0, 0, students[randomValue]);
            const item = this.surveyStudents[0];
            item.master = this.randomIndex(1);
          }
        }, 1000 * this.randomIndex(20));
      }
    }
  },
  beforeDestroy () {
    clearTimeout();
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-screen-question {
  min-height: 100%;

  /deep/ .van-field__label {
    width: 70px;
  }
}
</style>
